import { Injectable, Inject, EventEmitter } from '@angular/core';
import { isPresent } from './util';
import { Keys } from './common/keys';
import { NavigationAction } from './navigation-action';
import { NavigationConfig, NAVIGATION_CONFIG } from './navigation-config';

const MIN_INDEX: number = 0;

/**
 * @hidden
 */
@Injectable()
export class NavigationService {
    public navigate: EventEmitter<number> = new EventEmitter<number>();
    public open: EventEmitter<any> = new EventEmitter();
    public close: EventEmitter<any> = new EventEmitter();
    public enter: EventEmitter<any> = new EventEmitter();
    public tab: EventEmitter<any> = new EventEmitter();
    public esc: EventEmitter<any> = new EventEmitter();

    private useLeftRightArrows: boolean;

    constructor(@Inject(NAVIGATION_CONFIG) config: NavigationConfig) {
        this.useLeftRightArrows = config.useLeftRightArrows;
    }

    public process(args: any): NavigationAction {
        const keyCode: number = args.keyCode;
        let index: number;
        let action: NavigationAction = NavigationAction.Undefined;

        if (args.altKey && keyCode === Keys.down) {
            action = NavigationAction.Open;
        } else if (args.altKey && keyCode === Keys.up) {
            action = NavigationAction.Close;
        } else if (keyCode === Keys.enter) {
            action = NavigationAction.Enter;
        } else if (keyCode === Keys.esc) {
            action = NavigationAction.Esc;
        } else if (keyCode === Keys.tab) {
            action = NavigationAction.Tab;
        } else if (keyCode === Keys.up || (this.useLeftRightArrows && keyCode === Keys.left)) {
            index = this.next({ current: args.current, start: args.max, end: args.min, step: -1 });
            action = NavigationAction.Navigate;
        } else if (keyCode === Keys.down || (this.useLeftRightArrows && keyCode === Keys.right)) {
            index = this.next({ current: args.current, start: args.min, end: args.max, step: 1 });
            action = NavigationAction.Navigate;
        } else if (keyCode === Keys.home) {
            index = MIN_INDEX;
            action = NavigationAction.Navigate;
        } else if (keyCode === Keys.end) {
            index = args.max;
            action = NavigationAction.Navigate;
        }

        if (action !== NavigationAction.Undefined) {
            this[NavigationAction[action].toLowerCase()].emit(index);
        }

        return action;
    }

    private next(args: { current: number, start: number, end: number, step: number }): number {
        if (!isPresent(args.current)) {
            return args.start;
        } else {
            return args.current !== args.end ? args.current + args.step : args.end;
        }
    }
}



